<template>
  <div :class="cardPadding + ' change-card d-flex flex-column'" :style="verticalSpacing">
    <v-img :class="imageMargin" :src="clientImage" aspect-ratio="2" contain></v-img>
    <a :href="clientSite" :class="`${changeDescClasses} text-center font-weight-bold`">
      <span style="font-size: 0.7em;">
        {{ clientName }}
      </span>
    </a>
  </div>
</template>

<script>
export default {
  props: ['clientName', 'clientImage', 'clientSite'],
  computed: {
    cardPadding() {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return 'pa-2';
        case 'sm':
          return 'pa-3';
        case 'md':
          return 'pa-4';
        case 'lg':
          return 'pa-5';
        default:
          return 'pa-6';
      }
    },

    imageMargin() {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return 'mb-2';
        case 'sm':
          return 'mb-3';
        case 'md':
          return 'mb-4';
        case 'lg':
          return 'mb-5';
        default:
          return 'mb-6';
      }
    },

    changeDescClasses() {
      const baseClasses = 'grey--text text--darken-4';

      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return `${baseClasses} body-1`;
        case 'sm':
          return `${baseClasses} title`;
        case 'md':
          return `${baseClasses} headline`;
        case 'lg':
          return `${baseClasses} display-1`;
        default:
          return `${baseClasses} display-1`;
      }
    },

    verticalSpacing() {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return 'margin-top: 10px; margin-bottom: 10px;';
        case 'sm':
          return 'margin-top: 15px; margin-bottom: 15px;';
        case 'md':
          return 'margin-top: 20px; margin-bottom: 20px;';
        case 'lg':
          return 'margin-top: 25px; margin-bottom: 25px;';
        default:
          return 'margin-top: 30px; margin-bottom: 30px;';
      }
    },
  },
};
</script>

<style scoped>
div .change-card {
  width: 100%;
  border-width: 15px 5px 5px 5px;
  border-color: #FFE52D !important;
  border-style: solid;
}
</style>
