<script>
import ClientCard from '../components/ClientCard.vue';

export default {
  computed: {
    titleSize() {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return 'title';
        case 'sm':
          return 'headline';
        case 'md':
          return 'display-1';
        case 'lg':
          return 'display-2';
        default:
          return 'display-2';
      }
    },

    titlePadding() {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return 'pb-3';
        case 'sm':
          return 'pb-5';
        case 'md':
          return 'pb-7';
        case 'lg':
          return 'pb-9';
        default:
          return 'pb-9';
      }
    },

    masonGutter() {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return '10px';
        case 'sm':
          return '15px';
        case 'md':
          return '20px';
        case 'lg':
          return '25px';
        default:
          return '30px';
      }
    },

    cardCols() {
      return this.$vuetify.breakpoint.smAndDown ? 2 : 3;
    },
  },

  components: { ClientCard },
};
</script>

